import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {IoInsightInventoryService} from "@generated/io-insight-inventory-service";
import {PageControls} from "@component/wizard/flow-wizard.component";
import * as ManagedObjectData from "@generated/inventory-node";
import {InventoryNode} from "@component/inventory/inventory-node";

@WizardPage(
      VsanUiUtils.getString("vsan.io.insight.wizard.selectTarget.title"),
      VsanUiUtils.getString("vsan.io.insight.wizard.selectTarget.description")
)
@Component({
   templateUrl: "select-target-page.html",
   styleUrls: ["select-target-page.scss"],
})
export class SelectTargetPageComponent implements OnInit {
   inventoryRoot: ManagedObject[];

   @InputProperty()
   clusterObject: ManagedObjectData.InventoryNode;

   @InputProperty()
   vmHostRef: ManagedObject;

   @InputProperty(InputProperty.required(false))
   preselectedMoRef: ManagedObject;

   @InputProperty()
   private pageControls: PageControls;

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.io.insight.wizard.selectTarget.noTargetSelected.error")))
   targetInventoryNodes: InventoryNode[] = [];

   private _hostRefs: ManagedObject[] = [];

   @OutputProperty()
   get hostRefs(): ManagedObject[] {
      return this._hostRefs;
   }

   @OutputProperty()
   get clusterRef(): ManagedObject {
      return this.clusterObject.moRef;
   }

   get selectAllNodes(): boolean {
      // for cluster all child hosts should be selected
      return ManagedObject.contextObject.type === ManagedObjectUtils.CLUSTER;
   }

   constructor(public ioInsightInventoryService: IoInsightInventoryService) {}

   ngOnInit(): void {
      this.inventoryRoot = [this.clusterRef];
   }

   nodesChanged(nodes: InventoryNode[]): void {
      this.resetPage();
      this.targetInventoryNodes = nodes;
      this.buildHostRefs();
   }

   private buildHostRefs(): void {
      this._hostRefs = this.targetInventoryNodes
            .filter(node => !node.data.isLeafNode && !node.childrenFetched)
            .map(node => node.data.nodeRef);
   }

   rootNodesReceived(nodes: InventoryNode[]): void {
      // expand parent host node, in order the preselected VM to be visible
      if (this.preselectedMoRef && this.preselectedMoRef.type === ManagedObjectUtils.VM) {
         nodes.filter(node => ManagedObject.areEqual(node.data.nodeRef, this.vmHostRef))
               .forEach(hostNode => hostNode.expanded = true);
      }
   }

   resetPage(): void {
      this.pageControls.clearValidationErrors();
      this.targetInventoryNodes = [];
   }
}