import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {InventoryNode} from "@component/inventory/inventory-node";
import {InventoryNode as ManagedObjectInfo} from "@generated/inventory-node";
import {ManagedObject} from "@service/managed-object";
import {DurationUnit} from "@pipe/DurationPipe";
import {TreeNode} from "@component/inventory/tree-view/tree-node";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {BasicVmData} from "@generated/basic-vm-data";
import {PageItem} from "@component/wizard/page-item";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {SummaryVmData} from "@generated/summary-vm-data";

@WizardPage(
      VsanUiUtils.getString("vsan.io.insight.wizard.summaryPage.title"),
      null,
)
@Component({
   templateUrl: "summary-page.html",
   styleUrls: ["summary-page.scss"],
})
export class SummaryPageComponent implements OnInit {
   nodes: TreeNode[];

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   instanceName: string;

   @InputProperty()
   duration: number;

   @InputProperty()
   durationUnit: DurationUnit;

   @InputProperty()
   targetInventoryNodes: InventoryNode[];

   @InputProperty()
   hostVms: SummaryVmData[];

   @OutputProperty()
   get durationSeconds(): number {
      return VsanDateTimeUtils.toSeconds(this.duration, this.durationUnit);
   }

   @OutputProperty()
   get hosts(): ManagedObject[] {
      return this.targetInventoryNodes.map(node => node.data.nodeRef);
   }

   @OutputProperty()
   get vms(): ManagedObject[] {
      const result: ManagedObject[] = [];
      this.targetInventoryNodes.map(node => {
         node.childNodes.filter(child => child.selected).forEach(child => result.push(child.data.nodeRef));
      });
      return result;
   }

   async ngOnInit() {
      this.nodes = this.convertToTreeNodes(this.targetInventoryNodes, this.hostVms);
   }

   get durationLabel(): string {
      if (this.durationUnit === DurationUnit.MINUTES) {
         return this.duration === 1 ?
               VsanUiUtils.getString("vsan.time.common.minute") :
               VsanUiUtils.getString("vsan.time.common.minutes", this.duration);
      } else {
         return this.duration === 1 ?
               VsanUiUtils.getString("vsan.time.common.hour") :
               VsanUiUtils.getString("vsan.time.common.hours", this.duration);
      }
   }

   private convertToTreeNodes = (entities: InventoryNode[], vms: SummaryVmData[]): TreeNode[] => {
      if (!entities || entities.length === 0) {
         return [];
      }
      return entities.map(entity => {
         if (!entity.data.isLeafNode && !entity.childrenFetched) {
            const childVms = vms.filter(vm => ManagedObject.areEqual(entity.data.nodeRef, vm.hostRef));
            const poweredOnVsanVms = childVms.filter(vm => vm.isPoweredOn && vm.isVsanVm);
            const name = VsanUiUtils.getString(
                  "vsan.io.insight.wizard.summary.targetNode.name",
                  entity.data.name, poweredOnVsanVms.length, childVms.length);
            return new TreeNode(
                  ManagedObjectInfo.Factory.create(entity.data.nodeRef, name, entity.data.iconShape),
                  this.convertVMToTreeNodes(poweredOnVsanVms),
            );
         } else {
            const name = entity.data.isLeafNode ? entity.data.name :
                  VsanUiUtils.getString(
                        "vsan.io.insight.wizard.summary.targetNode.name",
                        entity.data.name, this.getSelectedChildrenCount(entity), entity.childNodes.length);
            const treeNode = new TreeNode(
                  ManagedObjectInfo.Factory.create(entity.data.nodeRef, name, entity.data.iconShape),
                  this.convertToTreeNodes(entity.childNodes.filter(child => child.selected), vms),
            );
            treeNode.isExpanded = true;
            return treeNode;
         }
      });
   };

   private convertVMToTreeNodes(vms: SummaryVmData[]): TreeNode[] {
      if (!vms && vms.length === 0) {
         return [];
      }
      return vms.map(vm => {
         return new TreeNode(ManagedObjectInfo.Factory.create(vm.vmRef, vm.name, vm.primaryIconId), null);
      });
   }

   getSelectedChildrenCount(node: InventoryNode): number {
      return node.childNodes.filter(child => child.selected).length;
   }

   get hasErrors(): boolean {
      return (this.pageControls as PageItem).errors.length > 0;
   }
}