import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {DurationUnit} from "@pipe/DurationPipe";
import * as ManagedObjectData from "@generated/inventory-node";
import {InventoryNode} from "@component/inventory/inventory-node";
import {DateFormat, VsanDateFormatterPipe} from "@pipe/VsanDateFormatterPipe";

@WizardPage(
      VsanUiUtils.getString("vsan.io.insight.wizard.settingsPage.title"),
)
@Component({
   templateUrl: "settings-page.html",
   styleUrls: ["settings-page.scss"],
})
export class SettingsPageComponent implements OnInit, OnValidate {
   private static readonly DATE_FORMATTER: VsanDateFormatterPipe = new VsanDateFormatterPipe();

   readonly INSTANCE_NAME_MAX_LENGTH: number = 256;

   private durationErrors: string[];

   @InputProperty()
   private clusterObject: ManagedObjectData.InventoryNode;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   targetInventoryNodes: InventoryNode[];

   @OutputProperty(OutputProperty.validatorProperty("isInstanceNameValid"))
   instanceName: string;

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.io.insight.wizard.settingsPage.noDuration.error")))
   duration: number = 10;

   @OutputProperty()
   durationUnit: DurationUnit = DurationUnit.MINUTES;

   ngOnInit(): void {
      const now: Date = new Date();
      let objectName: string = this.clusterObject.name;

      if (this.targetInventoryNodes.length === 1) {
         const selectedHost: InventoryNode = this.targetInventoryNodes[0];

         const selectedVMs: InventoryNode[] = selectedHost.childNodes.filter(x => x.selected);
         // Only 1 vm is selected, then use vm name
         if (selectedVMs.length === 1) {
            const selectedVm: InventoryNode = selectedVMs[0];
            objectName = selectedVm.data.name;
         } else {
            // Only 1 host with multiple vms is selected, then use host name
            objectName = selectedHost.data.name;
         }
      }

      this.instanceName = VsanUiUtils.getString("vsan.io.insight.wizard.settingsPage.defaultName", objectName,
                  SettingsPageComponent.DATE_FORMATTER.transform(now, DateFormat.NO_SECONDS));
   }

   onDurationValidationErrors(errors: string[]) {
      this.pageControls.clearValidationErrors();
      this.durationErrors = errors;
      this.pageControls.addValidationErrors(this.durationErrors);
   }

   validate(): string[] {
      return this.durationErrors && this.durationErrors.length
            ? this.durationErrors
            : null;
   }
}