import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {SummaryPageComponent} from "./summary-page.component";
import {SelectTargetPageComponent} from "./select-target-page.component";
import {SettingsPageComponent} from "./settings-page.component";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {NewIoInsightWizardContextData} from "./new-io-insight-wizard-context.data";
import {CreateIoInsightService} from "@generated/create-io-insight-service";

@Component({
   templateUrl: "new-io-insight-wizard.html",
   selector: "vsan-new-io-insight-wizard"
})
export class NewIoInsightWizardComponent implements OnInit {
   /**
    * Holds wizard context data, shared between all the pages.
    */
   context: NewIoInsightWizardContextData;
   errorMessage: string;
   services: any[] = [CreateIoInsightService];

   async ngOnInit() {
      this.setWizardContext();
   }

   private setWizardContext = () => {
      this.context = {
         objectRef: ManagedObject.contextObject,
         preselectedMoRef: this.canPreselctMoRef() ?
               ManagedObject.contextObject:
               null
      }
   }

   private canPreselctMoRef() : boolean {
      return ManagedObject.contextObject.type === ManagedObjectUtils.HOST ||
            ManagedObject.contextObject.type === ManagedObjectUtils.VM;
   }

   pages: PageSet[] = [
      new PageSet([SelectTargetPageComponent]),
      new PageSet([SettingsPageComponent]),
      new PageSet([SummaryPageComponent]),
   ];
}